* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
.App {
  display: flex;
  font-family: sans-serif;
  text-align: center;
  background-color: rgb(228, 213, 231);
  flex-direction: column;
  align-items: center;
  height: 100vmax;
}

h3 {
  display: inline;
}

.delete {
  border-radius: 10px;
  color: #ffffff;
  background-color: red;
}

.itemInput {
  margin-bottom: 10px;
}
.users {
  width: 50%;
  background-color: rgb(99, 214, 218);
  margin: 15px;
  padding: 5px;
  text-align: center;
  border-radius: 5px;
}
.post {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  border-radius: 5px;
  border: 2px solid azure;
  margin: 5px;
  background-color: powderblue;
}
.userMessage {
}
.actionBtn {
  cursor: pointer;
  padding: 10px;
  margin-left: 10px;
}

.user-profile {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 20px;
}

.user-profile button {
  margin-left: 10px;
  padding: 10px;
}

.user-profile img {
  border-radius: 50%;
  margin-right: 10px;
}

.login-btn {
  margin: 20px;
  padding: 10px;
}
